<template>
  <div>
    <section class="banner">
      <h1 :class="{'opacity-0': transparent}">CAREERS</h1>
    </section>
    <div class="bg-gray-200 py-20">
      <p class="w-full md:w-1/2 text-primary mx-auto text-center text-xl md:text-3xl px-4">
        We believe in hiring the best, and for us a dynamic 
        personality and partnership potential are as integral 
        as professional excellence. Because every associate is 
        on the partnership track from day one, top-notch training 
        and a commitment to help them build a sustainable long-term 
        career are priorities.
      </p>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    transparent: false
  }),
  methods: {
    handleScroll() {
      if (window.scrollY > 200) {
        this.transparent = true
      } else {
        this.transparent = false        
      }
    }
  },  
  mounted () {
    window.addEventListener('scroll', this.handleScroll);
  },
  unmounted () {
    window.removeEventListener('scroll', this.handleScroll);
  }
}
</script>

<style scoped>
.banner{
  height: 90vh;
  background-image: url('../../assets/img/careers.jpg');
  background-position: center;
  @apply w-screen bg-fixed bg-cover flex items-end p-20;
}
h1{
  @apply text-white text-3xl tracking-wide leading-none opacity-100 duration-200;
}
@screen sm{
  h1{
    @apply text-6xl;
  }
}
</style>