<template>
  <section class="bg-gray-200 text-primary">
    <div class="container py-20">
      <div class="text-center uppercase font-sans font-bold">
        now hiring
      </div>
      <div class="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6 my-10">
        <div 
          v-for="(item, i) in result" 
          :key="item.id" class="cursor-pointer" 
          @click="$router.push({name: 'detail-career', params: {id: item.id}})"
          data-aos="fade-up"
          :data-aos-delay="200*i">
          <div class="content-img">
            <img :src="'http://api.badranayalaw.credeva.id/uploads/picture/'+item.image" alt="highlight"/>
          </div>
          <div class="title">
            {{ item.judul }}
          </div>
        </div>
      </div>
      <div @click="result=data" class="text-center">
        <button class="btn btn-home btn-lg">
          view more
        </button>
      </div>
    </div>
    <!--  -->
    <div class="container py-20">
      <Subscribe/>
    </div>
  </section>
</template>

<script>
import Subscribe from '../Subscribe'
import http from '@/config/http'
export default {
  components: {
    Subscribe
  },
  data: () => ({
    data: [],
    result: []
  }),
  methods: {
    getData: async function () {
      http.get('career')
        .then(res => {
          this.data = res.data.data
          this.result = this.data.slice(0, 2)
        })
    },
  },
  created() {
    this.getData()
  }
}
</script>

<style scoped>
.content-img{
  max-height: 600px;
  @apply w-full overflow-hidden;
}
.content-img img{
  transform: scale(1);
  transition-timing-function: ease-in-out;
  @apply duration-500;
}
.content-img img:hover{
  transform: scale(1.2);
  transition-timing-function: ease-in-out;
  @apply duration-500;
}
.title{
  @apply text-2xl mt-4 text-center;
}
</style>